import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import BindResource from './views/privilege/BindResource.vue'
import BindVirtualResource from './views/privilege/BindVirtualResource.vue'
import BundleTemplateManage from './views/template/BundleTemplateManage.vue'
import ChannelTemplateManage from './views/template/ChannelTemplateManage.vue'
import BundleManage from './views/bundle/BundleManage.vue'
import AddBundle from './views/bundle/AddBundle.vue'
import ModifyBundle from './views/bundle/ModifyBundle.vue'
import ConfigBundle from './views/bundle/ConfigBundle.vue'
import LayerNodeManage from './views/layernode/LayerNodeManage.vue'
import AddLayerNode from './views/layernode/AddLayerNode.vue'
import ModifyLayerNode from './views/layernode/ModifyLayerNode.vue'
import FolderManage from './views/folder/FolderManage.vue'
import SerInfoAndNode from './views/serInfo/SerInfoAndNode.vue'
import UserBindBundle from './views/bind/UserBindBundle.vue'

let routes = [
  {
    path: '/404',
    component: NotFound,
    name: '',
    hidden: true
  },
  {
    path: '/',
    component: Home,
    name: '',
    iconCls: 'el-icon-message',//class
    children: [
      { path: '/main', component: Main, name: '', hidden: true },
      { path: '/BundleManage', component: BundleManage, name: '' },
      { path: '/AddBundle', component: AddBundle, name: '',hidden: true },
      { path: '/ModifyBundle', component: ModifyBundle, name: '',hidden: true },
      { path: '/ConfigBundle', component: ConfigBundle, name: '',hidden: true },
      { path: '/BundleTemplateManage', component: BundleTemplateManage, name: '' },
      { path: '/ChannelTemplateManage', component: ChannelTemplateManage, name: '', hidden: true},
      { path: '/LayerNodeManage', component: LayerNodeManage, name: '' },
      { path: '/AddLayerNode', component: AddLayerNode, name: '',hidden: true },
      { path: '/ModifyLayernode', component: ModifyLayerNode, name: '',hidden: true },
      { path: '/BindResource', component: BindResource, name: '' },
      { path: '/BindVirtualResource', component: BindVirtualResource, name: '',hidden: true },
      { path: '/FolderManage', component: FolderManage, name: '' },
      { path: '/SerInfoAndNode', component: SerInfoAndNode, name: '' },
      { path: '/UserBindBundle', component: UserBindBundle, name: ''}
    ]
  },
  {
    path: '*',
    hidden: true,
    redirect: { path: '/404' }
  }
];

export default routes;



