<template>
  <section>

    <el-tabs v-model="activeTabName" @tab-click="handleTabClick" style="float:left; width:100%;">
      <el-tab-pane label="" name="LayernodeManage"></el-tab-pane>
      <el-tab-pane label="" name="AddLayernode"></el-tab-pane>
    </el-tabs>

    <div style="float: left;width: 100%">
      <el-select size="small" v-model="filters.type" placeholder="" style="float: left;margin-left: 10px;width:200px;">
        <el-option v-for="item in typeOptions" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>
      <el-input size="small" v-model="filters.keyword" style="float: left;margin-left: 30px;width:200px;" placeholder=""></el-input>
      <el-button size="small" type="info" @click="getNodes" style="float: left;margin-left: 30px;"></el-button>

      <el-button size="small" type="danger" @click="handleMultiDelete" style="float: left;margin-left: 30px;"></el-button>
    </div>

    <!---->
    <el-table :data="nodes" v-loading="nodeTableLoading" @selection-change="handleSelectionChange" style="float: left;width: 100%;margin-top: 20px;">
      <el-table-column width="100" type="selection"></el-table-column>
      <el-table-column prop="name" label="" width="200" sortable>
      </el-table-column>
      <el-table-column prop="type" label="" width="200" sortable>
        <template slot-scope="scope">
          <div><span v-text="typeObj[scope.row.type]"></span></div>
        </template>
      </el-table-column>
      <el-table-column prop="nodeUid" label="ID" width="300" sortable>
      </el-table-column>
      <el-table-column prop="ip" label="IP" width="200" sortable>
      </el-table-column>
      <el-table-column width="150" label="">
        <template slot-scope="scope">
          <div v-if="scope.row.onlineStatus=='ONLINE'" style="height:20px;width:20px;border-radius:50%;background-color:#0f0"></div>
          <div v-else style="height:20px;width:20px;border-radius:50%;background-color:#f00"></div>
        </template>
      </el-table-column>

      <el-table-column label="" width="200">
        <template slot-scope="scope">
          <el-button type="text" @click="handleModify(scope.row)" size="small"></el-button>
          <el-button type="text" @click="handleDelete(scope.row)" size="small"></el-button>
          <el-button type="text" @click="gotoMonitor(scope.row)" size="small" v-if="scope.row.monitorUrl"></el-button>
          <el-button type="text" size="small" v-else disabled></el-button>
          <el-button type="text" @click="gotoAdmin(scope.row)" size="small" v-if="scope.row.netUrl"></el-button>
          <el-button type="text" size="small" v-else disabled></el-button>
        </template>
      </el-table-column>
    </el-table>

  </section>
</template>

<script type="text/ecmascript-6">
  import { getNodes,deleteNodes} from '../../api/api';

  export default {
    data() {
      return {
        activeTabName : "LayernodeManage",
        nodes: [
        ],
        typeOptions : [
          {
            value : "",
            label : ""
          },
          // {
          //   label : "JV210",
          //   value : "ACCESS_JV210"
          // },
          // {
          //   label : "CDN",
          //   value : "ACCESS_CDN"
          // },
          // {
          //   label : "IPC",
          //   value : "ACCESS_IPC"
          // },
          {
            label : "JV230",
            value : "ACCESS_JV230"
          },
          {
            label : "",
            value : "ACCESS_TVOS"
          },
          {
            label : "",
            value : "ACCESS_MIXER"
          },
          {
            label : "",
            value : "ACCESS_MOBILE"
          },
          {
            label : "",
            value : "ACCESS_JV210"
          },
          {
            label : "A",
            value : "ACCESS_CDN"
          },
          {
            label : "B",
            value : "ACCESS_VOD"
          },
          {
            label : "",
            value : "ACCESS_IPC"
          },
          {
            label : "",
            value : "ACCESS_STREAMMEDIA"
          },
          {
            label : "",
            value : "ACCESS_NETWORK"
          },
          {
            label : "",
            value : "ACCESS_DISPLAYCTRL"
          },
          {
            label : "",
            value : "ACCESS_S100"
          },
          //  JV210 
          {
            label : "",
            value : "ACCESS_VODPROXY"
          }
        ],
        typeObj : {
          // ACCESS_JV210 : "JV210",
          // ACCESS_CDN : "CDN",
          // ACCESS_IPC : "IPC",
          ACCESS_MOBILE : "",
          ACCESS_JV230 : "JV230",
          ACCESS_TVOS : "",
          ACCESS_MIXER : "",
          ACCESS_JV210 : "",
          ACCESS_CDN : "A",
          ACCESS_VOD : "B",
          ACCESS_IPC : "",
          ACCESS_STREAMMEDIA : "",
          ACCESS_NETWORK : "",
          ACCESS_DISPLAYCTRL : "",
          ACCESS_S100 : "",
          ACCESS_VODPROXY : ""
        },
        filters: {
          type : '',
          keyword: ''
        },
        nodeTableLoading: false,
        multipleSelection : []
      }
    },
    methods: {
      gotoMonitor:function(row){
        var self = this;
        window.open(row.monitorUrl);
      },
      gotoAdmin:function(row){
        var self = this;
        window.open(row.netUrl);
      },
      handleTabClick(tab, event) {
        if("LayernodeManage" !== tab.name){
          this.$router.push('/' + tab.name);
        }
      },
      //
      getNodes : function(){
        let param = {
          type : this.filters.type,
          keyword : this.filters.keyword
        };
        this.nodeTableLoading = true;
        getNodes(param).then((res) => {
          if (res.errMsg) {
            this.$message({
              message: res.errMsg,
              type: 'error'
            });
          } else {
            this.nodes = res.nodes;
          }

          this.nodeTableLoading = false;
        });
      },
      handleModify : function(row){
        this.$router.push({
          path: '/ModifyLayernode',
          query: {
            id: row.id
          }
        });
      },
      handleDelete : function(row){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let param = {
            nodeIds : row.id
          };

          deleteNodes(param).then(res => {
            if(res.errMsg){
              this.$message({
                message: res.errMsg,
                type: 'error'
              });
            } else {
              this.$message({
                message: "",
                type: 'success'
              });
              this.getNodes();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleMultiDelete : function(){
        if(!this.multipleSelection.length){
          this.$message({
            message: '',
            type: 'warning'
          });
          return;
        }

        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let nodeIds = [];
          for(let selection of this.multipleSelection){
            nodeIds.push(selection.id);
          }
          let param = {
            nodeIds : nodeIds.join(",")
          };

          deleteNodes(param).then(res => {
            if(res.errMsg){
              this.$message({
                message: res.errMsg,
                type: 'error'
              });
            } else {
              this.$message({
                message: "",
                type: 'success'
              });
              this.getNodes();
            }
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      },
      handleSelectionChange : function(val){
        this.multipleSelection = val;
      },
    },
    mounted() {
    var self = this;
    this.$nextTick(function() {
      self.$parent.$parent.$parent.$parent.$parent.setActive('/LayernodeManage');
    });
      this.getNodes();
    }
  }

</script>

<style>
</style>



